
.md-button {
  display: inline-block;
  width: 110px;
  height: auto;

  border: 1px solid var(--primary-text-color);
  border-radius: 5px;
  padding: 10px;
  text-transform: uppercase;
  background-color: transparent;
  align-items: center;
  text-align: center;

  font-family: Roboto, sans-serif;
  font-style: normal;
  @include font(500, 12px, 16px, 0.03em);
  color: var(--primary-text-color);
  cursor: pointer;
  

  &:not(:last-child) {
    margin-right: 20px;
  }

  // &:hover,
  // &:focus {
  //   background-color: var(--accent-color);
  //   border: none;
  // }


  &.swich-on {
    border: none;
    padding: 6px 27px;
    background-color: var(--accent-color);
    color: var(--white-text-color);
    
  
    
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: var(--accent-color);
    }
  }

}


  
 // ===768+====

@include tablet {
  .md-button {
    width: auto;
    padding-left: 20px;
    padding-right: 20px;
    &.swich-on {
      width: auto;
      padding: 10px 13px;
    }

    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}