.gallery {
  padding: 20px 0;
    &__list,
    &__list--library {
    display: flex;
    flex-wrap: wrap;
    row-gap: 20px;
    justify-content: center;
  }
  &__item {
    text-decoration: none !important;
    border: 1px solid #eeeeee;
    border-radius: 5px;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.25);
    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

    // &:not(:last-child) {
    //     margin-right: 16px;
    //   }
    
    //   &.item:nth-child(n + 4) {
    //     margin-bottom: 0px;
    //   }
    
    &:hover,
    &:focus {
      box-shadow: 0px 8px 8px 5px rgba(0, 0, 0, 0.25);
      cursor: pointer;
    }
    body.night & {
        border: 1px solid #ff6b0177;
      &:hover,
      &:focus {
        box-shadow: 0px 0px 4px 4px #ff6b0142;
      }
    }
  }

  &__link {
    display: block;
  }

  &__img {
    display: block;
    width: 280px;
    height: 402px;
    margin-bottom: 10px;
    border-radius: 5px;
  }
}

.films{
  &__name {
    color: var(--primary-text-color);
    text-transform: uppercase;
    font-family: Roboto, sans-serif;
    @include font(500, 12px, 16px, 0.02em);
    font-style: normal;
    margin-left: 5px;

    body.night & {
      color: #fff;
    }
  }

  &__genre {
    color: var(--accent-color);
    font-family: Roboto, sans-serif;
    @include font(500, 12px, 16px, 0.02em);
    font-style: normal;
    margin-left: 5px;
  }

  &-rating {
    color: var( --white-text-color);
    font-family: Roboto, sans-serif;
    @include font(500, 12px, 14px, 0.02em);
    font-style: normal;
    background-color: var(--accent-color);
    border-radius: 5px;
  }
}


// ====768+====

@include tablet {
  .gallery{
    padding: 60px 0;
    &__list,
    &__list--library{
      justify-content: start;
      gap: 32px;
    }

    &__img{
      width: 336px;
      height: 455px;
      margin-bottom: 8px;
    } 
  }
}

// ====1280+====

@include desktop {
  .gallery{
    &__list,
    &__list--library{
      justify-content: start;
      column-gap: 16px;
    }

    &__img{
      width: 394px;
      height: 574px;
      margin-bottom: 12px;
    } 
  }

  .films{
    &__name {
      @include font(500, 20px, 24px, 0.02em);
    }

    &__genre {
      @include font(500, 20px, 24px, 0.02em);
    }
  }
}